/* eslint-disable react/jsx-pascal-case */
import { Redirect, withRouter } from "react-router-dom";
import { Side_menu } from "../../components";
import style from "../css/CreateDocument.module.css";

function Console({ children, auth, Subscription }) {
  console.log(Subscription);
  return !auth ? (
    <> Error 404 </>
  ) : (
    <div className={style.ConsoleWrapper}>
      {" "}
      {Subscription === 0 ? (
        <Redirect to="/Subscribtion" />
      ) : (
        <>
          <Side_menu /> {children}{" "}
        </>
      )}{" "}
    </div>
  );
}

export default withRouter(Console);
